// Google Form Submit
async function submitGoogleForm(formData: {
  name: string;
  phone: string;
  email: string;
  message: string;
  pagePath: string;
  campaign: string;
  id: string;
}) {
  const formUrl = `https://docs.google.com/forms/d/e/${formData.id}/formResponse`;

  const payload = new URLSearchParams();
  payload.append("entry.466204223", formData.name.trim());
  payload.append("entry.32405555", formData.phone.trim());
  payload.append("entry.2122071872", formData.email.trim());
  payload.append("entry.176904452", formData.message.trim());
  payload.append("entry.1591426887", formData.pagePath);
  payload.append("entry.688569624", formData.campaign);

  try {
    await fetch(formUrl, {
      method: "POST",
      mode: "no-cors",
      body: payload.toString(),
    });
    return true;
  } catch (err) {
    console.error("Form submission error:", err);
    return false;
  }
}

